@import url(https://fonts.googleapis.com/css2?family=Do+Hyeon&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  header {
    font-size: 1rem;
    font-family: 'Do Hyeon';
  }
  header,
  form {
    min-height: 20vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }

body {
    background-image: linear-gradient(180deg, #f6d365 0%, #0fd0f1 100%);
    color: rgb(2, 17, 1);
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    min-height: 100vh;
}

    

img {
    max-width: 300px;
}

.App{
   
}
.search{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 10px;
    margin-bottom: 30px;
}
input{
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
}
.beaches {
    -webkit-justify-content: space-between;
            justify-content: space-between;
    float: left;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 40px;
    padding-top: 10px;
    text-align: center;
}

.container{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding-top: 10px;
    width: 100%;
}
